<template>
  <div class="spyj-page">
    <headerBar :header-data="headerData" />
    <!-- 项目信息 begin -->
    <div class="fixed-project">
      <div class="list">
        <h3>{{ dbxq.lcslmc }}</h3>
        <div class="info">
          <span>
            <i class="iconfont iconlianxiren" />
            {{ dbxq.preUsers }}
          </span>
          <span>
            <i class="iconfont iconshijian" />
            {{ dbxq.showTime }}
          </span>
          <span>
            <i class="iconfont iconpingbiaobaogao" />
            {{ dbxq.rwmc }}
          </span>
        </div>
      </div>
    </div>
    <!-- 项目信息 end -->
    <div class="container-mini">
      <div class="content">
        <div class="common-main box">
          <div class="txyj">
            <div class="txyj-tit">
              <span v-if="dbxq.thyjbt!=null && dbxq.thyjbt===1" class="text-danger">*</span>
              {{ dbxq.taskCommentLabel }}
              <div class="label-text warn">不同意</div>
              <span class="more" @click="showCyyPopup()">选择常用语
                <i class="iconfont iconarrowright" />
              </span>
              <!--              <span class="more" @click="saveCyy()">保存常用语-->
              <!--              </span>-->
            </div>
            <div class="txyj-content">
              <textarea v-model="spyj" :placeholder="'请输入'+dbxq.taskCommentLabel" />
            </div>
          </div>
        </div>
      </div>
      <div class="common-space" />
      <div class="foot-btn fixed">
        <ul>
          <!-- <li><van-button type="primary" plain round  @click="subimitSPYJ()">提交</van-button></li> -->
          <li><van-button type="info" size="large" round @click="subimitSPYJ()">不同意，退回本次审批</van-button></li>
        </ul>
      </div>
    </div>

    <transition name="router-slid" mode="out-in">
      <router-view />
    </transition>

    <!-- 选择常用语popup beign -->
    <van-popup
      v-model="showCyy"
      position="bottom"
      round
    >
      <van-picker
        show-toolbar
        :columns="cyyList"
        value-key="nr"
        @cancel="showCyy = false"
        @confirm="xzCyyOnConfirm"
      />
    </van-popup>
    <!-- 选择常用语popup end -->

  </div>
</template>

<script>
import headerBar from '@/components/header/headerBar'
import { mapState, mapMutations } from 'vuex'
import { pageRouter } from '@/common/js/mixin.js'

export default {
  name: 'DbsxBackOneStep',
  components: {
    headerBar
  },
  mixins: [pageRouter],
  data() {
    return {
      spyj: '',
      dbxq: {},
      headerData: {
        title: '待办审批',
        leftUrl: 'back',
        leftText: ' '
      },
      showCyy: false,
      cyyList: [],
      pageno: 1, // 页
      totalpageno: 0 // 总页数
    }
  },
  computed: {
    ...mapState({
      // listItem: state => state.jbrdbsx.listItem,
      txBizid: state => state.jbrdbsx.txBizid,
      userInfo: state => state.mh.mhUserInfo
    })
  },
  // watch: {
  //   // // 检测搜索条件发生变化，并进行搜索
  //   cyy(newQuery) {
  //     // this.search(newQuery)
  //     this.spyj = newQuery
  //   }
  // },
  created() {
    this.getDbxq()
    this.getcyylist()
  },
  methods: {
    ...mapMutations([
      'jbrdbsx/SET_DBSX_CYY',
      'jbrdbsx/SET_Refresh'
    ]),
    getDbxq() {
      this.$store.dispatch('jbrdbsx/getDbxqAction', {
        bizid: this.txBizid // 单点登录传过来的执行人id
      }).then(res => {
        this.dbxq = res
      }).catch(() => {
      })
    },
    getcyylist() {
      this.$store.dispatch('jbrdbsx/getCyyListAction', {
        pageno: this.pageno,
        id: this.userInfo.id
      }).then(res => {
        this.cyyList = res.data
        this.pageno = res.pageno
        this.totalpageno = res.totalpageno
      }).catch(() => {
      })
    },
    saveCyy() {
      if (this.spyj != null && this.spyj.length !== 0) {
        this.$store.dispatch('jbrdbsx/saveCyyAction', {
          nr: this.spyj,
          ryid: this.userInfo.id
        }).then(res => {
          this.$toast('保存成功！')
          this.getcyylist()
        }).catch(() => {
        })
      } else {
        this.$toast(this.dbxq.taskCommentLabel + '为空，不能保存为常用语!')
      }
    },
    xzCyyOnConfirm(value) {
      this.spyj = value.nr
      this.showCyy = false
    },
    showCyyPopup() {
      this.showCyy = true
    },
    subimitSPYJ() {
      this.clickBackSP()
    },
    clickBackSP() {
      let spyjValidate = true
      if (this.dbxq.thyjbt != null && this.dbxq.thyjbt === 1) {
        if (this.spyj === null || this.spyj.length === 0) {
          spyjValidate = false
        }
      }
      if (spyjValidate) {
        this.$store.dispatch('jbrdbsx/sendBackOneStepSP', {
          id: this.txBizid,
          taskComment: this.spyj
        }).then(res => {
          this.$toast('退回成功！')
          setTimeout(() => {
            this.$router.push('/DbsxIndex')
            this.SET_Refresh('true')
          }, 2000)
        }).catch(error => {
          // this.loading = false
          // this.pageStyles = 3
          this.$toast(error)
        })
      } else {
        this.$toast('请设置审批意见')
      }
    },
    goCyy() {
      this.$router.push({
        name: 'cyy' })
    }
  }

}

</script>

<style lang="css" scoped>
    .nav-bar{box-shadow: none;}
    .page-list{padding:0 12px;}
</style>
